import React, { useState, useEffect, useReducer } from 'react'
import ItemCard from './CardOfItem/ItemCard';
import styled from 'styled-components';
import { Routes, Route  } from "react-router-dom";
import Korzina from './Korzina/Korzina';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';

const Main = ({setCounter, counter}) => {
    const [data, setData] = useState([]);
    const [tovar, setTovar] = useState([]);

    let filtered;
    
    const RemoveDublicate = () => {
        if(tovar !== null){
            const ids = tovar.map(o => o.id);
            filtered = tovar.filter(({id}, index) => !ids.includes(id, index + 1))
        }
    }

    useEffect(() => {
        const setLocal = async () => {
            if(await tovar.length !== 0){
                localStorage.setItem('items', JSON.stringify(tovar));
                RemoveDublicate()
            }else{
                localStorage.removeItem('items')
            }
        }
        setLocal()
    },[tovar])
    useEffect(() => {
        const items = JSON.parse(localStorage.getItem('items'));
        if (items) {
            setTovar(items);
        }
      }, []);

    const [openLoad, setOpenLoad] = useState(true);

    const handleCloseLoad = () => {
        setOpenLoad(false);
    };

    RemoveDublicate();


    const CountTovar = () => {
        if(filtered !== undefined){
            setCounter((prev) => prev = filtered.reduce((acc, item) => acc+=item.count,0))
        }
    }
    useEffect(() => {
        const FetchCards = () => {
            let countItems = 20;
            let url = `https://dummyjson.com/carts/?limit=${countItems}`;
            fetch(url)
                .then(res => res.json())
                .then(res => setData(res.carts))
                .then(() => handleCloseLoad())
        }
        FetchCards();
        CountTovar();
    },[])
    useEffect(() => {
        CountTovar();
    },[tovar])

  return (
    <div>
        <Backdrop
            sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
            open={openLoad}
            >
        <CircularProgress color="inherit" />
      </Backdrop>
        <Routes>
            <Route 
                path="/" 
                element={<CardsListItems 
                    tovar={tovar}
                    setTovar={setTovar}
                    data={data}
                    setData={setData}/>} />

            <Route 
                path="korzina" 
                element={<Korzina
                    filtered={filtered}
                    tovar={tovar}
                    setTovar={setTovar} />} />
        </Routes>
        
    </div>
  )
}


const CardsListItems = ({data, tovar, setTovar, setData}) => {
   
    const [wrapData , setWrapData] = useState([]);

    useEffect(() => {
        if(data){
            setWrapData([...data]);
        }
    },[data])

    const priceOne = () => {
        setWrapData(() => data.filter(el => el.products[0].price < 15))
    }
    const priceTwo = () => {
        setWrapData(() => data.filter(el => el.products[0].price > 10 && el.products[0].price < 50))

    }
    const priceThree = () => {
        setWrapData(() => data.filter(el => el.products[0].price > 50))
     
    }
    const priceAll = () => {
        setWrapData(() => data.filter(el => el));
    }
    return(
        <div style={{paddingBottom:'20px'}}>
            <Box
            sx={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                '& > *': {
                m: 1,
                },
            }}
            >
            <ButtonGroup variant="outlined" aria-label="outlined button group">
                <Button 
                    onClick={priceOne}
                >{`Price < 15`}</Button>
                <Button

                    onClick={priceTwo}
                >{`Price 10-50`}</Button>
                <Button
                    onClick={priceThree}
                >{`Price > 50`}</Button>
                <Button
                    onClick={priceAll}
                >{`All Price`}</Button>
            </ButtonGroup>
            </Box>

            <ListWrap>
                {wrapData.length > 0 ? wrapData.map((el, id) => 
                <ItemCard 
                    key={el.id}
                    title={el.products[0].title}
                    id={el.id}
                    tovar={tovar}
                    setTovar={setTovar}
                    bye={true}
                    price={el.products[0].price} />
                ) : <span>Loading...</span>}
            </ListWrap>
        </div>
    )
}

const ListWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
`

export default Main