import React, {useEffect, useState} from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import ButtonGroup from '@mui/material/ButtonGroup';
import Box from '@mui/material/Box';
import Skeleton from '@mui/material/Skeleton';
import { useInView } from 'react-intersection-observer';
import styled from 'styled-components';



const ItemCard = ({title, id, setTovar, tovar, bye, price, count}) => {

  const { ref, inView } = useInView({
    threshold: 1, 
    triggerOnce: true,
  });

  const [active , setActive] = useState(false);

  useEffect(() => {
    setActive((prev) => prev = tovar.filter(el => el.id === id).some(el => el.id === id))
  },[tovar])

  return (
    <Wrap ref={ref}>
      {inView ?
        <Card style={{paddingBottom: "40px" ,backgroundColor : "#fff", height : "100%" , position: "relative"}} sx={{ minWidth: 275 }}>
        <CardContent>
          <h3>{title}</h3>
        </CardContent>
        <CardActions  style={{display:'flex', justifyContent:'space-between',position : "absolute" ,left: "0", bottom: "0",width: '100%'}}>
          {bye  ? 
            <WrapperBtnGroupWithInpust>
              {active ? 
              <Button 
                variant="contained" 
                size="small"
                disabled>
                  buyed
              </Button>
               :
              <Button onClick={() => 
                {if(tovar === null){
                  setTovar(() => [{title: title, id: id , price:price, count: 1}])
                }else{
                  setTovar((prev) => 
                  [...prev , {title: title, id: id , price:price, count: 1}])
              }}
              } variant="contained" aria-label="outlined primary button" size="small">Buy
            </Button>
              
              }

                

              <span style={{marginLeft: '10px'}}>Price: {price}</span>
            </WrapperBtnGroupWithInpust>
                :
            <WrapperBtnGroupWithInpust>
              <Button onClick={() => {setTovar(tovar.filter(el => el.id !== id))}}
                  style={{backgroundColor: 'red'}} variant="contained" aria-label="outlined primary button" size="small">Delete</Button>
              <span style={{marginLeft: '2px'}}>Price: {price}</span>
              <ButtonGroup variant="contained" aria-label="outlined primary button group">
                <Button
                  style={{borderRight:'none', backgroundColor:'yellow', color:'#000'}}
                  onClick={() => setTovar((prev) => {
                    prev.forEach(el => {
                      if(el.id === id){
                        el.count = el.count - 1
                      }
                      if(el.count <= 0){
                        prev = tovar.filter(el => el.id !== id)
                      }
                    })
                    return [...prev]
                  })}
                  >-</Button>
                  <CountElementInCounter>
                    {count}
                  </CountElementInCounter>
                <Button 
                  style={{backgroundColor:'yellow', color:'#000'}}
                  onClick={() => setTovar((prev) => {
                    prev.forEach(el => {
                      if(el.id === id){
                        el.count = el.count + 1
                      }
                    })
                    return [...prev]
                  })}>+</Button>
              </ButtonGroup>
            </WrapperBtnGroupWithInpust>
        }
        </CardActions>
        </Card> 
        : 
        <Box style={{width: "300px",  height: "100%"}} >
          <Skeleton />
          <Skeleton animation="wave" />
          <Skeleton animation={false} />
        </Box>
      }
        
    </Wrap>
  );
}
const CountElementInCounter = styled.span`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
`

const Wrap = styled.div`
   margin: 5px;
   width: 300px;
   border: 2px solid #000;
`
const WrapperBtnGroupWithInpust = styled.div`
  display:flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`

export default ItemCard