import React from 'react';
import styled from 'styled-components';
import ItemCard from '../CardOfItem/ItemCard';


const Korzina = ({tovar, setTovar, filtered}) => {

  return(
    <>
    {filtered !== undefined || filtered ? 
      <CountText>Total Price :
        <span>
          {filtered ? filtered.reduce((acc, el) => acc += el.price * el.count,0) : null}
        </span>
      </CountText>: null }

      <WrapFlex>
          {/* {filtered 
          ?
          <span>No items here...</span> 
          : 
          filtered.map((el, id) => 
            <ItemCard 
                key={el.id}
                title={el.title}
                id={el.id}
                tovar={tovar}
                setTovar={setTovar}
                bye={false}
                price={el.price}
                count={el.count} />
            )} */}
         {typeof filtered !== "undefined" ? 

             filtered.map((el, id) => 
              <ItemCard 
                  key={el.id}
                  title={el.title}
                  id={el.id}
                  tovar={tovar}
                  setTovar={setTovar}
                  bye={false}
                  price={el.price}
                  count={el.count} />
              ): null}



      </WrapFlex>
    </>
  )
}
const CountText = styled.div`
  text-align: center;
  font-size:25px;
  font-weight: 700;
`

const WrapFlex = styled.div`
  display: flex;
  flex-wrap:wrap;
  padding: 10px;
`
export default Korzina